body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 6px dotted #0cc3a8af;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
  box-shadow: 5px 5px 10px #21dcaa80, inset -5px -5px 10px #ffffff80;
}

.second-hand, .minute-hand {
  transform-origin: top;
  position: absolute;
  top: 50%;
  left: 50%;
}

.second-hand {
  background-color: #50bc3d;
  width: 2px;
  height: 35%;
  animation: 60s linear infinite rotate-second;
}

.minute-hand {
  background-color: #31ad69;
  width: 4px;
  height: 40%;
  animation: 5s linear infinite rotate-minute;
}

@keyframes rotate-second {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

@keyframes rotate-minute {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

